rom './Block'\nimport Vector from './Vector'\n\n/**\n * Created by Daniel on 2017-09-18.\n */\nexport default class Player extends Entity {\n  /**\n   *\n   * @param {string} name\n   * @param {number} x\n   * @param {number} y\n   * @param {number} width\n   * @param {number} height\n   * @param {CollisionBounds} collisionBox\n   * @param {Material} material\n   */\n  constructor (name, x, y, width, height, material) {\n    super(x, y, width, height, material)\n    this.name = name\n    this.lives = 3\n    this.speed = 3.6\n    this.running = false\n    this.jumping = false\n    this.grounded = false\n    this.history = []\n    this.friction = 0.8\n    this.gravity = 0.2\n  }\n\n  /**\n   *\n   * @param game\n   */\n  move (game) {\n    if (this.keyActionsRegister['w'] || this.keyActionsRegister[' ']) {\n      if (!this.jumping && this.grounded) {\n        game.audioManager.playSound('jump')\n        this.jumping = true\n        this.grounded = false\n        this.velocity.y = -this.speed * 2\n      }\n    }\n\n    if (this.keyActionsRegister['a']) {\n      if (this.velocity.x > -this.speed) {\n        this.velocity.x--\n      }\n    }\n\n    if (this.keyActionsRegister['d']) {\n      if (this.velocity.x < this.speed) {\n        this.velocity.x++\n      }\n    }\n    this.velocity.x *= this.friction\n    this.velocity.y += this.gravity\n\n    this.grounded = false\n\n    game.current.blocks.forEach(block => {\n      const direction = this.checkCollision(this, block)\n\n      if (direction === 'l' || direction === 'r') {\n        this.velocity.x = 0\n        this.jumping = false\n      } else if (direction === 'b') {\n        this.grounded = true\n        this.jumping = false\n      } else if (direction === 't') {\n        this.velocity.y *= -1\n      }\n    })\n\n    if (this.grounded) {\n      this.velocity.y = 0\n    }\n\n    this.position.add(this.velocity)\n  }\n\n  goBack () {\n    if (this.history.length !== 0) {\n      this.position.set(this.history[this.his